.store_base_banner {
  background-image: url(../images/qa.png);
  height: 304px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;

  .container {
    height: 100%;
    display: flex;
    align-items: flex-end;
    h2 {
      font-size: 64px;
      align-items: center;
      .icon_location {
        margin-left: 24px;
      }
    }
  }
}
.stores_area {
  padding-top: 16px;
  padding-bottom: 304px;
  .choose_city {
    font-size: 24px;
    margin-bottom: 32px;
    align-items: center;
    .citys {
      width: 29.18%;
      padding: 10px;
      border: 1px solid #ced4da;
      font-size: 18px;
      margin-left: 20px;
    }
  }
  .stores {
    flex-wrap: wrap;
    .store {
      width: calc(33.3333% - 20px);
      //   總margin為60px(每個物件間距30，共有兩個間距) / 物件數 3 = 20
      margin-bottom: 32px;
      &:not(:nth-of-type(3n + 1)) {
        // 所有的元素margin-left: 30px;，
        // 3的倍數加一不要有margin-left
        margin-left: 30px;
      }
      p {
        border: 1px solid #00000020;
        font-size: 24px;
        padding: 12px 24px;
      }
      ul {
        border: 1px solid #00000020;
        padding: 16px 24px;
        li {
          align-items: flex-start;
          font-size: 20px;
          margin-bottom: 9px;
          &:last-child{
              margin-bottom: 0;
          }

          img {
            width: 21px;
            margin-right: 16px;
          }
        }
      }
      a {
        font-size: 19px;
        text-align: center;
        background-color: #000;
        color: $text_white;
        padding: 20px 0;
        &:hover {
          background-color: $bg_primary_color;
        }
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .store_base_banner {
    background-position: 35% center;
    .container {
      h2 {
        font-size: 48px;
        .icon_location {
          margin-left: 16px;
        }
      }
    }
  }
  .stores_area {
    .choose_city {
      font-size: 16px;
      margin-bottom: 24px;

      .citys {
        width: 34.34%;
        padding: 12px 16px;
        font-size: 16px;
      }
    }
    .stores {
      flex-wrap: wrap;
      .store {
        width: calc(50% - 15px);
        //   總margin為30px(每個物件間距30，共有1個間距) / 物件數 2 = 20
        margin-bottom: 32px;
        &:not(:nth-of-type(3n + 1)) {
          margin-left: 0px;
        }
        &:not(:nth-of-type(2n + 1)) {
          // 所有的元素margin-left: 30px;，
          // 2的倍數加一不要有margin-left
          margin-left: 30px;
        }
        p {
          font-size: 16px;
          padding: 8px 16px;
        }
        ul {
          padding: 16px;
          li {
            font-size: 14px;

            img {
              width: 20px;
            }
          }
        }
        a {
          font-size: 14px;
          padding: 16px 0;
        }
      }
    }
  }
}
@media screen and (max-width: 767px) {
  .store_base_banner {
    height: 143px;

    .container {
      h2 {
        font-size: 28px;

        .icon_location {
          margin-left: 8px;
        }
      }
    }
  }
  .stores_area {
    padding-top: 8px;
    padding-bottom: 56px;
    .choose_city {
      font-size: 14px;
      margin-bottom: 20px;
      .citys {
        width: 73.86%;
        padding: 8px 16px;
        font-size: 14px;
        margin-left: 12px;
      }
    }
    .stores {
      .store {
        width: 100%;
        margin-bottom: 16px;
        &:not(:nth-of-type(3n + 1)) {
          margin-left: 0px;
        }
        &:not(:nth-of-type(2n + 1)) {
          margin-left: 0px;
        }
      }
    }
  }
}
